.footerContainerMain {
  color: #383b41;
  display: flex;
  font-size: 12px;
  font-weight: 600;
  padding-top: 50px;
  padding-bottom: 45px;
}
.f1 {
  width: 92%;
  display: block;
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
}
.f2 {
  display: flex;
  align-items: center;
  width: calc(100% + 24px);
  margin: -12px;
  flex-wrap: wrap;
  box-sizing: border-box;
}
.f3 {
  margin: 0;
  box-sizing: border-box;
  height: 80px;
  width: 114px;
}
.f4 {
  margin: 0;
  box-sizing: border-box;
  height: 80px;
  width: 41%;
  padding: 12px;
}
.f5 {
  margin: 0;
  box-sizing: border-box;
  height: 80px;
  width: 20%;
  padding: 12px;
}
.f6 {
  margin: 0;
  box-sizing: border-box;
  height: 80px;
  width: 25%;
}
@media only screen and (max-width: 900px) {
  .f2 {
    display: block;
  }
  .f4 {
    width: 100%;
  }
  .f5 {
    width: 100%;
  }
}
