.dashboard0 {
  height: 150vh;
  width: 14rem;
  background-color: #fff;
  float: left;
  overflow: auto;
}

.dashboard01 {
  height: 50px;
  width: 100%;
  margin: 0px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: white;
  font-family: sans-serif;
}
.dashboard02 {
  flex: 30%;
  display: grid;
  place-items: center;
}
.dashboard03 {
  flex: 70%;
  font-size: 25px;
  color: #000;
  font-weight: 700;
  text-align: center;
}
.dashboard-ul {
  height: auto;
  width: 100%;
  padding: 0px;
}
.dashboard-li {
  cursor: pointer;
  height: 48px;
  width: 100%;
  list-style-type: none;
  margin: 0px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: #000;
  font-size: 20px;
  /*font-family: sans-serif;*/
}
/* .dashboard-li :hover {
