#active {
  font-weight: bold;
  color: #fff;
}

.form-inline {
  width: 35px;
  height: 35px;
  margin: 27px 52px 0px 0px;
}
.navbar {
  background-color: rgb(222, 233, 235);
  margin-bottom: 42px;
}

@media only screen and (max-width: 992px) {
  #dashboard-0 {
    height: 200vh;
    width: 100%;
  }
  .dashboard0 {
    display: none;
  }
  .navbar {
    display: none;
  }
}

